import { Formik, FormikProps } from 'formik';
import React, { Dispatch, SetStateAction, useEffect, useState } from 'react';
import { Button, Form, Grid, Segment } from 'semantic-ui-react';
import { TEMPLATE_CLOSE, TEMPLATE_DELIMETER, TEMPLATE_OPEN } from './utils/constants';
import { EInputTypes, EValidationDataTypes } from './utils/enums/inputEnums';
import { inputFormatter } from './utils/inputFormatter';
import { TInputTypeObject, TOutputFormat } from './utils/types/setupTypes';
import { FloorTable } from './tables/FloorTable';
import * as Yup from 'yup';
import { FormValuesDisplay } from './FormValuesDisplay';
import { DashedDateField, DashedLangDateField } from '../../shared/DateField';
import UnitDropdown from '../../shared/UnitDropdown';
import { DashedLangInput, DashedNormalInput, DashedNormalInputIm } from '../../shared/DashedFormInput';
import { squareUnitOptions } from '../../../utils/dataUtils';
import { IFormFieldList } from '../interfaces';
import api from '../../../utils/api';
import { showErrorToast } from '../../../utils/functionUtils';
import {
	normalNepaliDate,
	validateNepaliDate,
	validateNormalNepaliDate,
	validateNullableNepaliDate,
	validateNullableNormalNumber,
	validateNullableNUmberField,
	validateNumber,
	validateString,
} from '../../../utils/validationUtils';
import { SaveButtonValidation } from '../../shared/SaveButtonValidation';

type TProps = {
	// data: string;
	// dataMapping: IFormFieldList[];
	inputTypes: TInputTypeObject[];
	postAction: (data: any) => void;
	output: TOutputFormat[];
	readOnlyFields: IFormFieldList[];
	loading: boolean;
	savePermission: boolean;
	applicationNo: string;
	// setOutput: Dispatch<SetStateAction<TOutputFormat[]>>;
};

const floorDummyData = [
	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
];

export const LayoutRenderer = ({ applicationNo, readOnlyFields, inputTypes, output, postAction, loading, savePermission }: TProps) => {
	const [appIdIndex, setAppIdIndex] = useState(0);
	useEffect(() => {
		const inputType = inputTypes.find((row) => row.columnName === 'application_no');
		if (inputType) {
			setAppIdIndex(inputType.id);
		}
	}, [inputTypes]);

	const generateSchema = () => {
		let schema = {};
		inputTypes!.forEach((inputType) => {
			const validation = inputType && inputType.meta && inputType.meta.validation;

			if (validation) {
				if (validation.required) {
					if (validation.dataType === EValidationDataTypes.STRING) {
						Object.assign(schema, {
							[inputType.id]: validateString,
						});
					} else if (validation.dataType === EValidationDataTypes.NUMBER) {
						Object.assign(schema, {
							[inputType.id]: validateNumber,
						});
					} else if (validation.dataType === EValidationDataTypes.NORMAL_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNormalNepaliDate,
						});
					} else if (validation.dataType === EValidationDataTypes.UNICODE_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNepaliDate,
						});
					}
				} else {
					if (validation.dataType === EValidationDataTypes.NUMBER) {
						Object.assign(schema, {
							[inputType.id]: validateNumber,
						});
					} else if (validation.dataType === EValidationDataTypes.NORMAL_DATE) {
						Object.assign(schema, {
							[inputType.id]: normalNepaliDate,
						});
					} else if (validation.dataType === EValidationDataTypes.UNICODE_DATE) {
						Object.assign(schema, {
							[inputType.id]: validateNullableNepaliDate,
						});
					}
				}
			}
		});
		return Yup.object().shape(schema);
	};

	const renderInput = (content: string, formikProps: FormikProps<any>) => {
		const [type, name] = inputFormatter(content);
		const inputType = inputTypes.find((row) => row.columnName === name);
		console.log(name, type);

		if (inputType) {
			const formFieldId = inputType.id;

			switch (type) {
				case EInputTypes.NEPALI_DATE:
					return (
						//@ts-ignore
						<DashedLangDateField
							inline
							name={formFieldId}
							setFieldValue={formikProps.setFieldValue}
							//@ts-ignore
							value={formikProps.values[formFieldId]}
							//@ts-ignore
							error={formikProps.errors[formFieldId]}
						/>
					);
				case EInputTypes.NORMAL_NEPALI_DATE:
					return <DashedDateField name={formFieldId} inline={true} label="" compact={false} />;
				case EInputTypes.FLOOR_TABLE:
					return <FloorTable floorArray={floorDummyData} inputType={inputTypes.find((obj) => obj.name === name)} />;
				case EInputTypes.DASHED_NORMAL_INPUT:
					//@ts-ignore
					return <DashedNormalInputIm name={formFieldId} />;
				case EInputTypes.UNIT_DROPDOWN:
					//@ts-ignore
					return <UnitDropdown name={'floorUnit'} options={squareUnitOptions} setFieldValue={formikProps.setFieldValue} />;
				default:
					return (
						//@ts-ignore
						<DashedLangInput
							name={formFieldId}
							setFieldValue={formikProps.setFieldValue}
							//@ts-ignore
							value={formikProps.values[formFieldId]}
							//@ts-ignore
							error={formikProps.errors[formFieldId]}
						/>
					);
			}
		} else {
			return null;
		}
	};

	return (
		<Formik
			initialValues={{}}
			validationSchema={generateSchema()}
			onSubmit={(values, { setSubmitting }) => {
				setSubmitting(true);

				let dataToSend = { ...values };
				if (appIdIndex) {
					dataToSend = { ...values, [appIdIndex]: applicationNo };
				}
				try {
					postAction(dataToSend);
					// if (this.props.success && this.props.success.success) {
					// 	handleSuccess(checkError(this.props.prevData), this.props.parentProps, this.props.success);
					// }
					setSubmitting(false);
					window.scrollTo(0, 0);
				} catch (err) {
					setSubmitting(false);
					window.scrollTo(0, 0);
					console.log('Error', err);
					showErrorToast('Something went wrong !!');
				}
				// setTimeout(() => {
				console.log(JSON.stringify(values, null, 2));
				//   setSubmitting(false);
				// }, 400);
				setSubmitting(false);
			}}
		>
			{(props) => (
				<Form loading={props.isSubmitting || loading}>
					<Segment style={{ textAlign: 'left' }}>
						<div>
							{output &&
								output.map((row) => (
									<React.Fragment key={row.id}>
										{row.input ? (
											renderInput(row.content, props)
										) : (
											<div
												style={{
													display: row.inline ? 'inline-block' : 'inline',
												}}
												dangerouslySetInnerHTML={{ __html: row.content }}
											/>
										)}
									</React.Fragment>
								))}
							{/* {formattedData && (
                )} */}
							{/* <div dangerouslySetInnerHTML={{ __html: formattedData }} /> */}
							{/* <Button
								primary
								content="Save"
								onClick={(e) => {
									e.preventDefault();
									props.handleSubmit();
								}}
							/> */}
							<SaveButtonValidation
								errors={props.errors}
								formUrl={''}
								hasSavePermission={savePermission}
								prevData={{}}
								hasDeletePermission={false}
								isSaveDisabled={false}
								handleSubmit={props.handleSubmit}
								validateForm={props.validateForm}
							/>
						</div>
					</Segment>
				</Form>
			)}
		</Formik>
	);
};
